import adventures from '@/components/adventures';

export default {
	components: {
		adventures
	},
	data() {
		return {
			orderInfo:{}
		}
	},
	onLoad(options) {
		let orderNum = options.orderNum;
		this.orderNum = orderNum;
		this.$request.ModelOrder.infoOder(orderNum).then(result => {
			this.orderInfo = result;
		})
	},
	methods: {
		
	}
}
